import Vue from "vue";
import Router from "vue-router";

const lazyLoad = view => () => import(`@/views/${view}.vue`);

Vue.use(Router);

const mainRoutes = [
    {
        path: "/",
        name: "Home",
        component: lazyLoad("Home")
    },
    {
        path: "/profile",
        name: "SelfProfile",
        component: lazyLoad("SelfProfile")
    },
    {
        path: "/profile/:nickname",
        name: "Profiles",
        component: lazyLoad("Profiles")
    },
    {
        path: "/premium",
        name: "PremiumMarket",
        component: lazyLoad("PremiumMarket")
    },
    {
        path: "/config",
        name: "Config",
        component: lazyLoad("Config")
    },
    {
        path: "/team",
        name: "Team",
        component: lazyLoad("Team")
    },
    {
        path: "/hunt",
        name: "Hunt",
        component: lazyLoad("Hunt")
    },
    {
        path: "/city",
        name: "City",
        component: lazyLoad("City")
    },
    {
        path: "/bag",
        name: "Bag",
        component: lazyLoad("Bag")
    },
    {
        path: "/recovery",
        name: "Recovery",
        component: lazyLoad("Recovery")
    },
    {
        path: "/market",
        name: "Market",
        component: lazyLoad("Market")
    },
    {
        path: "/jobs",
        name: "Jobs",
        component: lazyLoad("Jobs")
    },
    {
        path: "/marketplace",
        name: "MarketPlace",
        component: lazyLoad("MarketPlace")
    },
    {
        path: "/bank",
        name: "Bank",
        component: lazyLoad("Bank")
    },
    {
        path: "/storagemarket",
        name: "StorageMarket",
        component: lazyLoad("StorageMarket")
    }
];

const blockRoutes = [
    {
        path: "/captcha",
        name: "Captcha",
        component: lazyLoad("Captcha")
    },
    {
        path: "/initialmonster",
        name: "InitialMonster",
        component: lazyLoad("InitialMonster")
    },
    {
        path: "/learn",
        name: "LearnMove",
        component: lazyLoad("LearnMove")
    },
    {
        path: "/evolve",
        name: "Evolve",
        component: lazyLoad("Evolve")
    }
];

const routes = [ ... mainRoutes, ... blockRoutes ];

const router = new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;